	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Philip Wadler, Professor of Theoretical Computer Science at the University of Edinburgh, contends that the foundations of computing lay in a coincidence: Church's lambda calculus (1933), Herbrand and Godel's recursive functions (1934), and Turing's machines (1935) all defined the same model of computation. \n\n\nHe gave this lecture on the occasion of Princeton University's centennial celebration of Alan Turing. Learn more at www.princeton.edu/turing\n\n#turingprinceton","meta":{"duration":"PT1H3M41S"},"presenter":{"name":null},"source":{"id":"2PJ_DbKGFUA","provider":"youtube"},"slug":"philip-wadler-churchs-coincidences","tag":["Philip Wadler","University of Edinburgh","Princeton","Alan Turing","computer science","lambda calculus","Turing machine","#turingprinceton"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/2PJ_DbKGFUA/default.jpg","height":90,"width":120}},"title":"Philip Wadler: Church's Coincidences"}}]}},"pathContext":{"slug":"university-of-edinburgh","tag":"University of Edinburgh"}}

